.contacts h3 {
  font-size: 18px;
  line-height: 27px;
  font-weight: 500;
}

.contacts a {
  color: $color-tangaroa;
  position: relative;
  display: block;
  padding-left: 28px;

  &::before {
    content: '';
    width: 18px;
    height: 22px;
    position: absolute;
    left: 0;
    bottom: 0;
    background-image: url('../img/sprite/map-mark.svg');
  }

  &.contacts__phone::before {
    height: 18px;
    background-image: url('../img/sprite/icon-phone.svg');
  }

  @include hover-focus {
    color: $color-azureish-white;
  }
}
.contacts p {
  margin: 0;
}
